import Product from "../models/productModel";
import Cart from "../models/cartModel";
import User from "../models/userModel";

// Ajouter un parfum
const addProduct = async (req, res) => {
  const { name, ml, price, description, gender, image } = req.body;
  try {
    const newProduct = new Product({
      name,
      ml,
      price,
      description,
      gender,
      image,
    });
    await newProduct.save();
    res.status(201).json(newProduct);
  } catch (error) {
    res.status(400).json({ message: error.message });
  }
};

// Supprimer un parfum
const deleteProduct = async (req, res) => {
  try {
    const productId = req.params.id;
    const deletedProduct = await Product.findByIdAndDelete(productId);
    if (!deletedProduct) {
      return res.status(404).json({ message: "Produit non trouvÃ©" });
    }
    res.json({ message: "Produit supprimÃ© avec succÃ¨s", deletedProduct });
  } catch (error) {
    res.status(500).json({ message: error.message });
  }
};

// Mise Ã  jour d'un produit
const updateProduct = async (req, res) => {
  try {
    const productId = req.params.id;
    const { name, ml, price } = req.body;
    const existingProduct = await Product.findById(productId);
    if (!existingProduct) {
      return res.status(404).json({ message: "Produit non trouvÃ©" });
    }

    existingProduct.name = name;
    existingProduct.ml = ml;
    existingProduct.price = price;

    const updatedProduct = await existingProduct.save();

    res.json({ message: "Produit mis Ã  jour avec succÃ¨s", updatedProduct });
  } catch (error) {
    res.status(500).json({ message: error.message });
  }
};
// RÃ©cupere un seul parfum
const getProductById = async (req, res) => {
  try {
    const productId = req.params.id;
    const product = await Product.findById(productId);
    if (!product) {
      return res.status(404).json({ message: "Produit non trouvÃ©" });
    }
    res.json(product);
  } catch (error) {
    res.status(500).json({ message: error.message });
  }
};

// RÃ©cuperer tous les parfums
const allProducts = async (req, res) => {
  try {
    const allProducts = await Product.find();
    res.json(allProducts);
  } catch (error) {
    res.status(400).json({ message: error.message });
  }
};

const addToCart = async (req, res) => {
  try {
    const product = await Product.findById({ _id: req.params.id });
    if (!product) {
      return res.status(404).json({ error: "Product not found" });
    }
    const user = await User.findById(req.params.userId);
    if (!user) {
      return res.status(404).json({ error: "User not found" });
    }

    let cart = await Cart.findById(user.userCart);
    if (!cart) {
      cart = await Cart.create({ products: [] });
      user.userCart.push(cart._id);
      user.save();
    }

    cart.products.push({ productId: product, quantity: 1 });
    await cart.save();
    res.json({ cart, message: "Product added successfully" });
  } catch (error) {
    res.status(500).json({ error: error.message });
  }
};

export {
  addProduct,
  allProducts,
  deleteProduct,
  updateProduct,
  getProductById,
  addToCart,
};
